const Covid = () => {

    const [data, getData] = useState([]);

    const getCovidData = async () => {
        const res = await fetch('https://api.covid19india.org/data.json');
        const actualData = await res.json();
        console.log(actualData.statewise);

        getData(actualData.statewise);
    }

    useEffect(() => {

        getCovidData();


    }, []);

    //setInterval(ValueUpd,1000);
    return (
        <>
            <div className = 'container-fluid mt-5'>
            <div className = 'main-heading'>
                <h1 className = 'HD1' >India Covid 19 Tracker Statwise        </h1>

            </div>
            <br/>
            <div className = 'table-responsive'> 
            <table className = 'table table-hover' styele ={{width :'100%'}}>
                <tr style = {{height:'5px', width :'100px'} }>
                    <th>State</th>
                    <th>Active</th>
                    <th>Confirmed</th>
                    <th>Death</th>
                    <th>Date</th>
                    <th>recovered</th>
                </tr>
                <tbody>
       
                    {data.map((val, ind) => {
                        return (
                            <tr>
                                <td>{val.state}</td>
                                <td>{val.active}</td>
                                <td>{val.confirmed}</td>
                                <td>{val.deaths}</td>
                                <td>{val.lastupdatedtime}</td>
                                <td>{val.recovered}</td>
                            </tr>
                        )
                    })}

                </tbody>
            </table>
            </div>
            </div>
     
        </>

    )
}

export default Covid;
