const Covid = () => {

    const [data1, getData] = useState();
    const [data2, getconfirmed] = useState();
    const [data3, getdeaths] = useState();
    const [data4, getrecovered] = useState();
    const [data5, getlastupdatedtime] = useState();

    const getCovidData = async () => {
        const res = await fetch('https://api.covid19india.org/data.json');
        const actualData = await res.json();
        console.log(actualData.statewise);

        getData(actualData.statewise[0].active);
        getconfirmed(actualData.statewise[0].confirmed);
        getdeaths(actualData.statewise[0].deaths);
        getrecovered(actualData.statewise[0].recovered);
        getlastupdatedtime(actualData.statewise[0].lastupdatedtime);
    }

    useEffect(() => {

        getCovidData();


    }, []);

    //setInterval(ValueUpd,1000);
    return (
        <>
        <h1 className = 'HD'>Covid Cases in India</h1>
        <div className = 'Container'>
            <div className="card1">
                <h1>Total Active Case</h1>
                <h2 >{data1}</h2>


            </div>
            <div className="card2">
                <h1>Total Confirmed Case</h1>
                <h2 >{data2}</h2>
            </div>
            <div className="card3">
                <h1>Total Deaths </h1>
                <h2 >{data3}</h2>
            </div>
            <div className="card4">
                <h1>Total Recovered</h1>
                <h2 >{data4}</h2>
            </div>
            <div className="card5">
                <h1>lastupdatedtime</h1>
                <h2 >{data5}</h2>

            </div>
            </div>

        </>

    )
}

export default Covid;
